		<uni-collapse>
			<uni-collapse-item title="什么是RPA？"  >
				<text>机器人流程自动化（Robotic Process Automation，下称“RPA”）技术通过模仿人工操作与判断，能够协助 人工完成日常繁琐的重复性工作（例如鼠标点击、文件下载、发送邮件、报表统计等）。</text>
			</uni-collapse-item>
			<uni-collapse-item title="RPA的作用"  >
				<text>RPA作为一种新兴的数字劳动力，可以辅助员工完成规则明确的重复性劳动，并且部署灵活，快速响应业务需求，大幅提升业务流程效率，同时能降低人工操作的错误风险，实现企业业务流程自动化和智能化，从而降本增效。</text>
			</uni-collapse-item>
			<uni-collapse-item title="RPA的优势"  >
				<text>
通常认为通过RPA产品构建机器人流程具有如下5个方面的优势：
全天候命：7*24小时，365天不间断运行
降本增效：缩短原本业务流程的人工处理时间
安全合规：严格按照集团、行业规定执行
稳定可控：规避人为操作风险，操作准确率100%
敏捷速赢：机器人流程开发周期短，版本迭代速度快					
				</text>
			</uni-collapse-item>
			<uni-collapse-item title="使用RPA构建机器人流程的价值"  >
				<text>
根据项目实施经验机器人流程通常的业务价值分为如下几个维度：
1.创造直接价值
2.挖掘潜在客户
3.降低操作风险
4.节约人力成本
5.提升处理效率
6.提升受理时效
7.提升数据安全
				</text>
			</uni-collapse-item>
			<uni-collapse-item title="集团RPA智能机器人平台项目简介"  >
				<text>
集团RPA智能机器人平台项目（以下简称“集团RPA项目”）自2020年8月执行实施以来，已累计完成一期、二期项目建设工作，同时三期项目建设工作也仍在稳步推进中。项目通过引进机器人流程自动化（RPA）技术，赋能集团各部门及子公司各类业务，覆盖包括8个集团总部部门与期货、资管子公司。
项目可以分为如下几个阶段：
尝试探索2020.10—2021.02
1.构建集团化RPA平台，探索机器人流程在集团总部业务场景的实施落地
2.构建机器人流程管控系统、RPA项目绩效集中展示系统
3.尝试在集团内部宣传与进一步推广机器人流程
弹射起步2021.03-2021.07
1.持续扩展集团RPA平台服务能力，完善集团RPA门户功能
2.扩大集团机器人流程覆盖范围，同步推进已上线流程的迭代优化
3.持续推进集团RPA管理制度标准化，加强RPA团队管理
小步快跑2021.08-2022.02
1.在短时间内，完成总计43个业务场景相关的机器人流程建设工作
2.持续推进RPA项目自有团队建设，逐步实现机器人流程开发的自主可控
敏捷速赢2022.03—2023.02
1.稳步推进机器人流程场景建设
2.尝试探索适合于集团推广的机器人流程业务场景
3.结合集团AI中台能力，尝试构建AI+RPA的IPA机器人流程
				</text>
