			<uni-collapse-item title="集团RPA项目亮点"  >
				<text>
1.统一管理
通过集团RPA门户，可以统一完成不同部门机器人流程的上线配置、流程启停等操作
2.实时监控
通过集团RPA门户，可实时监控机器人流程运行状态并对执行的异常情况进行实时预警
3.权限隔离
以RBAC模型构建RPA门户用户权限，实现不同用户间的机器人流程共享与数据隔离
4.RPaaS
集团RPA平台主动将部分机器人流程封装为服务，供周边系统直接调用
5.服务集成
集团RPA平台与集团内部系统、外部第三方服务平台完成对接，具体拓展服务能力
6.绩效展示
通过可视化界面及企业BI，实时刷新并展现RPA平台的重要指标
				</text>
			</uni-collapse-item>
			<uni-collapse-item title="典型机器人流程场景简介"  >
				<text>
诚信档案查询机器人流程
1.业务背景
在我司客户的网上业务受理及审核过程中，业务人员需要根据客户三要素（存管处从集运系统获取，集团客服中心从网开系统获取）至失信记录查询官网查询结果，截图保存；再上传至集运系统(存管处）、网开系统（集团客服中心）。
2.机器人流程
业务人员在审核相关业务过程中，通过集运系统/网开系统向RPA实时发送查询请求（API），机器人流程接受到请求后至失信记录网站进行查询，截图后RPA通过API接口将截图回传至原系统。
3.RPA价值
通过RPass方式为外部系统提供实时服务
对比人工处理耗时，机器人流程执行效率提升90%
相关交易在RPA平台的日均业务处理量超4000笔
日均释放人力超80小时
自动化费用稽核
1.业务背景
业务人员需对已收集的报销信息与发票信息按既定规则进行稽核：涉及的票据数量多、稽核规则较广，人工审核较为耗时。
2.机器人流程
业务人员核验配置文件与业务规则后启动机器人。机器人流程读取配置文件与业务规则后，登录费用稽核系统，进入审批中心完成报销信息与发票信息处理。
3.RPA价值
支持超过30条稽核规则，支持动态配置，避免人工误差，100%准确率
机器人流程运行时长超900个小时/月，节省人工700人天/月
机器人7*24小时运作，业务办理时效显著提升
				</text>
